.footer {
  &__social {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 10px;

    i {
      font-size: 20px;

      @media (min-width: 768px) {
        font-size: 30px;
      }

      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
  &__slogan {
    text-align: center;

    img {
      width: 201px;
    }
  }
}
