$color-white: #fff;
$color-black: #000;
$color-grey-dark: #999;
$color-primary: green;

.popup {
	height: 100vh;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba($color-black, 0.9);
	z-index: 9999;

	// opacity: 0;
	// visibility: hidden;

	transition: all 0.3s;

	&__content {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		width: 75%;
		background-color: darken($color: $color-white, $amount: 10);
		// box-shadow: 0 2rem 4rem rgba($color-black, 0.2);
		border-radius: 5px;
		overflow: hidden;
		display: table;
		// opacity: 0;
		// transform: translate(-50%, -50%) scale(0.5);
		// transition: all 0.5s 0.2s;
		@media (min-width: 768px) {
			width: 85%;
		}

		@media (min-width: 992px) {
			width: 65%;
		}

		@media (min-width: 1280px) {
			width: 50%;
		}
	}

	&__left {
		width: 33.333333%;
		display: table-cell;

		@media (max-width: 425px) {
			display: none;
		}
	}

	&__right {
		width: 66.666667%;
		display: table-cell;
		vertical-align: top;
		padding: 1.5rem 2.5rem;
		color: $color-black;

		@media (min-width: 768px) {
			padding: 2.5rem 2rem 1rem;
		}

		@media (max-width: 425px) {
			padding: 3rem 1rem 1rem;
		}
	}

	&__img {
		display: block;
		height: auto;
		width: 300px;

		@media (min-width: 768px) {
			width: 277px;
		}
	}

	&__close {
		&,
		&:link,
		&:visited {
			color: $color-grey-dark;
			position: absolute;
			top: 1rem;
			right: 1.5rem;
			font-size: 1.5rem;
			text-decoration: none;
			display: inline-block;
			transition: all 0.2s;
			line-height: 1;
			cursor: pointer;

			@media (min-width: 768px) {
				top: 0.5rem;
				right: 0.5rem;
			}
		}
		&:hover {
			color: $color-primary;
		}
	}

	&__title {
		font-size: 24px;
		margin: 0rem;

		@media (min-width: 768px) {
			font-size: 20px;
		}

		@media (max-width: 425px) {
			font-size: 18px;
		}
	}

	&__subtitle {
		font-size: 16px;
		margin-bottom: 1rem;

		@media (min-width: 768px) {
			margin-bottom: 0.5rem;
		}
	}

	&__list {
		list-style: none;
		padding: 0px;
		margin-bottom: 2rem;

		@media (min-width: 768px) {
			margin-bottom: 1rem;
		}
	}

	&__item {
		margin-bottom: 0.5rem;
		label {
			color: #668192;
			display: block;
			margin: 0;
		}
	}

	&__acciones {
		text-align: right;
	}
}

.btn {
	&,
	&:link,
	&:visited {
		text-transform: uppercase;
		text-decoration: none;
		padding: 0.5rem 1rem;
		display: inline-block;
		border-radius: 5px;
		font-size: 16px;

		transition: all 0.2s;
		backface-visibility: hidden;
		position: relative;

		border: none;
		cursor: pointer;

		@media (max-width: 425px) {
			padding: 0.5rem 0.5rem;
		}
	}

	&:hover {
		transform: translateY(-0.3rem);
		box-shadow: 0 1rem 2rem rgba($color-black, 0.2);

		&::after {
			transform: scaleX(1.4) scaleY(1.6);
			opacity: 0;
		}
	}

	&:active,
	&:focus {
		outline: none;
		transform: translateY(-0.1rem);
		box-shadow: 0 0.5rem 1rem rgba($color-black, 0.2);
	}

	&__color {
		color: $color-white;
		background-color: #668192;
	}
}
