.about {
  &__founder {
    text-align: center;
    margin-bottom: 10px;

    // @media (min-width: 768px) {
    //   text-align: left;
    // }

    &-image {
      display: inline-block;
      width: 200px;
      height: 200px;
      overflow: hidden;
      // border-radius: 50%;

      @media screen and (min-width: 768px) {
        width: 300px;
        height: 300px;
        margin-bottom: 20px;
      }

      img {
        width: inherit;
      }
    }
  }

  &__list {
    span {
      display: block;
      font-size: 14px;
      color: color-grey;

      &:first-child {
        margin-bottom: 10px;
      }
    }
  }

  &__article {
    h4:first-child {
      text-align: center;
      margin-bottom: 20px;

      @media (min-width: 768px) {
        text-align: left;
      }
    }
  }
}
