.equipo {
  margin-bottom: 20px;

  &__grid {
    flex-direction: row;
    justify-content: center;
    margin-top: 40px;
  }

  &__icon {
    text-align: center;
    font-size: 2rem;
  }

  &__subtitle {
    text-align: center;
    font-size: 22px;
    margin: 5px 0 10px;

    @media (min-width: 768px) {
      font-size: 26px;
    }
  }

  &__item {
    font-size: 16px;
    text-align: center;

    @media (min-width: 768px) {
      font-size: 18px;
      text-align: justify;
    }
  }

  &__item {
    span {
      display: block;
      font-size: 14px;
    }
  }
}
