.song {
  background-color: $color-white;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10px;
  overflow: hidden;
  padding: 5px;
  position: relative;
  width: 90px;

  @media (min-width: 768px) {
    width: 125px;
  }

  @media (min-width: 992px) {
    width: 170px;
  }

  &__image {
    width: 100%;
    height: 100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: $color-white;
    bottom: -90px;
    color: $color-black;
    height: 90px;
    position: absolute;
    width: calc(100% - 10px);
    transition: all 0.3s ease-in-out;

    @media (min-width: 768px) {
      bottom: -125px;
      height: 125px;
    }

    @media (min-width: 992px) {
      bottom: -80px;
      height: 80px;
    }

    span {
      display: block;
      text-align: center;
      font-size: 14px;
    }
  }

  &:hover &__content,
  &__image:hover &__content {
    transform: translate(0, -90px);

    @media screen and (min-width: 768px) {
      transform: translate(0, -125px);
    }

    @media screen and (min-width: 992px) {
      transform: translate(0, -80px);
    }
  }
}
