.heading__two {
  font-size: 2rem;
  font-weight: 700;

  @media (min-width: 768px) {
    font-size: 3.5rem;
  }
}

.heading__three {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 10px;

  @media (min-width: 768px) {
    font-size: 2rem;
  }
}

.heading__four {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 10px;

  @media (min-width: 768px) {
    font-size: 1.7rem;
  }
}

.heading__six {
  color: $color-black;
  font-size: 1.13em;
  line-height: 31px;
  text-align: right;

  @media screen and (min-width: 768px) {
    line-height: 48px;
  }

  span {
    background: $color-white;
    padding: 5px 3px;

    @media (min-width: 768px) {
      padding: 10px;
    }
  }
}

.paragraph {
  line-height: 1.7;

  @media (min-width: 768px) {
    font-size: 18px;
  }
}

.list {
  list-style: none;
  padding: 0;
}
